import React from 'react';
import { useForm, ValidationError } from '@formspree/react';

import Layout from "../components/layouts/layout"
import Container from "../components/container"
import Section from "../components/section"
import Seo from "../components/seo"

function ContactForm() {
  const [state, handleSubmit] = useForm("xlevgrdn");
  if (state.succeeded) {
      return (
        <Layout title={"Kontakt"}>
          <Section>
            <Container>
              <p>Vielen Dank f√ºr deinen Input!</p>
            </Container>
          </Section>
        </Layout>
      )
  }
  return (
    <Layout title={"Kontakt"}>
      <Section>
        <Container>
          <form onSubmit={handleSubmit}>
          <div className="field">
            <label className="label" htmlFor="email">
              Email Adresse
            </label>
            <div className="control">
              <input
                className="input"
                id="email"
                type="email"
                name="email"
                placeholder="Email Adresse"
                />
            </div>
          </div>
          <ValidationError
            prefix="Email"
            field="email"
            errors={state.errors}
          />
          <div className="field">
            <label className="label" htmlFor="email">
              Nachricht
            </label>
            <textarea
              className="textarea"
              id="message"
              name="message"
              placeholder="Deine Nachricht"
            />
          </div>
          <ValidationError
            prefix="Message"
            field="message"
            errors={state.errors}
          />
          <div className="field">
            <div className="control">
              <button type="submit" disabled={state.submitting}>
                Abschicken
              </button>
            </div>
          </div>
        </form>
        </Container>
      </Section>
    </Layout>
  );
}
function App() {
  return (
    <ContactForm />
  );
}
export default App;

export const Head = () => <Seo title="Kontakt" />
